export function ripple(event) {
  const htmlElement = event.currentTarget;
  const circle = document.createElement("span");
  const { top, left, width, height } = htmlElement.getBoundingClientRect();
  const diameter = Math.max(width, height);
  const radius = diameter / 2;

  circle.style.width = circle.style.height = `${diameter}px`;
  circle.style.left = `${event.clientX - left - radius}px`;
  circle.style.top = `${event.clientY - top - radius}px`;
  circle.classList.add("cyw-ripple");

  const ripple = htmlElement.querySelector(".cyw-ripple");

  if (ripple) {
    ripple.remove();
  }
  htmlElement.appendChild(circle);
}
